import '@/common/iconfont.css';
import Nav from '@/layout/nav';
import Search from '@/pages/search/index';

export default {
	data() {
		return {
			navList: [{ name: '推荐', url: '' }, { name: '更新', url: '' }],
			navIndex: 0,
			menuIndex:0,
			nav: [
				{ name: '排行榜', icon: '\ue61b;', url: '/pages/index/index' },
				{ name: 'VIP专区', icon: '\ue60e', url: '/pages/list/index' },
				{ name: '书单', icon: '\ue690', url: '/pages/bookshelf/index' },
				{ name: '积分商城', icon: '\ue617', url: '/pages/my/index' }
			],
			sex: 0,
			showSearch:false,
			bookList:[],
		};
	},
	components: {
		'v-nav': Nav,
		'v-search': Search,
	},
	methods: {
		goToSearch() {
			console.log(123);
		},
		setShowsearch(){
			this.showSearch=true
			uni.hideTabBar({
				
			})
		},
		setMenuindex(index){
			this.menuIndex = index
			uni.showToast({
				icon:'none',
				title:'功能还在开发中。。。'
			})
		}
	},
	beforeCreate() {
		uni.request({
			url: 'http://192.168.2.100/showBooklist/0/12', //仅为示例，并非真实接口地址。
			data: {},
			header: {},
			success: res => {
				this.bookList = res.data;
			}
		});
	},
};
